header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}
.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}


.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;

}
/* ================= redes sociales ==========*/
.header__socials{
display: flex;
flex-direction: column;
align-items: center;
gap: 0.8rem;
position: absolute;
left: 0;

}

.header__socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}
/* ================= imagen ==========*/
.us{
    background:linear-gradient(var(--color-primary),transparent);
    width: 17rem;
    height: 30rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 4rem;
    border-radius: 6rem 6rem 0 0;
    padding: 2.5rem 1.5rem 1.5rem 1.5rem;
}

/* ================= scroll down==========*/
.scroll-down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}
/* ================= media queries (dispositivos medianos)==========*/
@media screen and (max-width:1024px) {
header{
    height: 100vh;
}
}


/* ================= media queries (dispositivos pequenos)==========*/
@media screen and (max-width:600px) {
header{
    height: 100%;    
}    
.header__socials,.scroll-down{
    display: none;

}
.container.header__container{
    width:var(--container-width-sm);
}
}